// bulma docs https://dev.to/1n0t/creacion-de-un-proyecto-svelte-bulma-sass-1o8i
import "../styles.bulma.scss";
import App from '../Calendar3Pod.svelte';

let calendars = [...document.querySelectorAll(".calendar-3pod")];
window.calendar3PodInit = function(){
    let calendars = [...document.querySelectorAll(".calendar-3pod")];
    init(calendars);
}
window.calendars3PodElements = [];
function init(calendars){
    calendars.map(( calendar )=>{
        if(!calendar.classList.contains("initialized-calendar")){
            let id = window.calendars3PodElements.length+1;
            new App({
                target: calendar,
                props: {
                    data: calendar.dataset,
                    calendar: calendar,
                    number_calendar: id,
                }
            });
            window.calendars3PodElements.push({
                calendar: calendar,
                id: id
            });
            calendar.classList.add("initialized-calendar");
        }
    })
}

init(calendars);


